/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, input, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
#root {
    margin: 15px;
}

header {
    background-color: blueviolet;
    height: 100px;
}

img {
    margin: 10px;
}

h1 {
    font-size: 38px;
    color: white;
    margin-left: 10px;
    margin-top: 25px;
    font-family: 'Lobster', cursive;
}

h2 {
    font-size: 20px;
    font-family: 'Happy Monkey', cursive;
}

li {
    font-family: 'Happy Monkey', cursive;
    font-size: 18px;
}

.main {
    height: 100vh;
    border: 2px solid blueviolet;
}

.btn.btn-primary {
    background: blueviolet;
    border: blueviolet;
    width: 100px;
    border-radius: 10px;
    margin-left: 15px;
}

input {
    width: 655px;
    height: 36px;
    border: 1px solid black;
    border-radius: 10px;
    margin-left: 17px;
    margin-top: 25px;
    text-align: center;
}

form {
    margin-bottom: 20px;
}

.recipe {
    display: grid;
    border: 2px solid #4b48d6;
    width: 770px;
    height: 75px;
    align-content: center;
    font-size: 32px;
    padding-left: 10px;
    font-family: 'Happy Monkey', cursive;
    margin-bottom: 25px;
}

.directory {
    border-right: 1px solid #4b48d6;
    height: 765px;

}

.detail {
    border-left: 1px solid #4b48d6;
    height: 765px;

}

.center {
    text-align: center;
}

.name {
    font-size: 42px;
    margin-top: 25px;
}
